import * as React from 'react';
import { Calendar, dateFnsLocalizer, View } from 'react-big-calendar';
import { format, parse, startOfWeek, getDay } from 'date-fns';
import { ja } from 'date-fns/locale';
import 'react-big-calendar/lib/css/react-big-calendar.css';
import { CalendarEvent } from '../../types/calendar';
import { groupConfig } from '../../config/groupConfig';
import { getUserColor } from '../../utils/userColors';

const locales = {
  'ja': ja,
};

const localizer = dateFnsLocalizer({
  format,
  parse,
  startOfWeek,
  getDay,
  locales,
});

interface CalendarViewProps {
  events: CalendarEvent[];
  currentUserEmail: string;
  onSelectEvent: (event: CalendarEvent) => void;
  onSelectSlot: (slotInfo: { start: Date; end: Date }) => void;
}

export const CalendarView: React.FC<CalendarViewProps> = ({
  events,
  currentUserEmail,
  onSelectEvent,
  onSelectSlot,
}) => {
  const [view, setView] = React.useState<View>('week');
  const [date, setDate] = React.useState(new Date());
  const calendarRef = React.useRef<HTMLDivElement>(null);

// 8
React.useEffect(() => {
    const scrollToMorning = () => {
      if (calendarRef.current && (view === 'day' || view === 'week')) {
        // 
        const timeGrid = calendarRef.current.querySelector('.rbc-time-content');
        if (timeGrid) {
          // 78
          const scrollPosition = 320; // 
          timeGrid.scrollTop = scrollPosition;
        }
      }
    };
  
    // DOM
    const timer = setTimeout(scrollToMorning, 150);
    return () => clearTimeout(timer);
  }, [view, date]);

  // react-big-calendar
  const calendarEvents = events.map(event => {
    // 
    const ownerMember = groupConfig.flatMap(g => g.members)
      .find(m => m.email === event.ownerEmail);
    const ownerName = ownerMember?.name || event.ownerEmail.split('@')[0];
    const isOwner = event.ownerEmail === currentUserEmail;
    
    return {
      id: event.id || `event-${Math.random()}`,
      title: isOwner ? event.subject : `${ownerName}: ${event.subject}`, // 
      start: new Date(event.start.dateTime),
      end: new Date(event.end.dateTime),
      resource: {
        ...event,
        isOwner: event.ownerEmail === currentUserEmail,
        ownerName: ownerName,
      },
    };
  });

  // 
  const eventStyleGetter = (event: any) => {
    const isOwner = event.resource.isOwner;
    const ownerEmail = event.resource.ownerEmail;
    
    // 
    const member = groupConfig.flatMap(g => g.members)
      .find(m => m.email === ownerEmail);
    const isResource = member?.type === 'resource';
    
    // 
    const color = getUserColor(ownerEmail, isOwner, isResource);
    
    return {
      style: {
        backgroundColor: color,
        borderColor: color,
        color: 'white',
        cursor: 'pointer',
      }
    };
  };

  // 
  const CustomToolbar = (toolbar: any) => {
    const goToBack = () => {
      toolbar.onNavigate('PREV');
    };

    const goToNext = () => {
      toolbar.onNavigate('NEXT');
    };

    const goToToday = () => {
      toolbar.onNavigate('TODAY');
    };

    const label = () => {
      const date = toolbar.date;
      
      // 
      if (toolbar.view === 'month') {
        return (
          <span style={{ fontSize: '18px', fontWeight: 'bold' }}>
            {format(date, 'yyyy M', { locale: ja })}
          </span>
        );
      } else if (toolbar.view === 'week') {
        // 
        const weekStart = startOfWeek(date, { locale: ja, weekStartsOn: 1 });
        const weekEnd = new Date(weekStart);
        weekEnd.setDate(weekEnd.getDate() + 6);
        
        return (
          <span style={{ fontSize: '18px', fontWeight: 'bold' }}>
            {format(weekStart, 'yyyy Md', { locale: ja })} - {format(weekEnd, 'Md', { locale: ja })}
          </span>
        );
      } else {
        // 
        return (
          <span style={{ fontSize: '18px', fontWeight: 'bold' }}>
            {format(date, 'yyyy Md (E)', { locale: ja })}
          </span>
        );
      }
    };

    return (
      <div style={{ 
        display: 'flex', 
        justifyContent: 'space-between', 
        alignItems: 'center',
        marginBottom: '20px',
        padding: '10px',
        borderBottom: '2px solid #ddd'
      }}>
        <div style={{ display: 'flex', gap: '10px' }}>
          <button 
            onClick={goToBack}
            style={{
              padding: '8px 16px',
              backgroundColor: '#f3f2f1',
              border: '1px solid #d1d1d1',
              borderRadius: '4px',
              cursor: 'pointer'
            }}
          >
             
          </button>
          <button 
            onClick={goToToday}
            style={{
              padding: '8px 16px',
              backgroundColor: '#0078d4',
              color: 'white',
              border: 'none',
              borderRadius: '4px',
              cursor: 'pointer'
            }}
          >
            
          </button>
          <button 
            onClick={goToNext}
            style={{
              padding: '8px 16px',
              backgroundColor: '#f3f2f1',
              border: '1px solid #d1d1d1',
              borderRadius: '4px',
              cursor: 'pointer'
            }}
          >
             
          </button>
        </div>

        {label()}

        <div style={{ display: 'flex', gap: '5px' }}>
          <button
            onClick={() => toolbar.onView('day')}
            style={{
              padding: '8px 16px',
              backgroundColor: toolbar.view === 'day' ? '#0078d4' : '#f3f2f1',
              color: toolbar.view === 'day' ? 'white' : 'black',
              border: '1px solid #d1d1d1',
              borderRadius: '4px',
              cursor: 'pointer'
            }}
          >
            
          </button>
          <button
            onClick={() => toolbar.onView('week')}
            style={{
              padding: '8px 16px',
              backgroundColor: toolbar.view === 'week' ? '#0078d4' : '#f3f2f1',
              color: toolbar.view === 'week' ? 'white' : 'black',
              border: '1px solid #d1d1d1',
              borderRadius: '4px',
              cursor: 'pointer'
            }}
          >
            
          </button>
          <button
            onClick={() => toolbar.onView('month')}
            style={{
              padding: '8px 16px',
              backgroundColor: toolbar.view === 'month' ? '#0078d4' : '#f3f2f1',
              color: toolbar.view === 'month' ? 'white' : 'black',
              border: '1px solid #d1d1d1',
              borderRadius: '4px',
              cursor: 'pointer'
            }}
          >
            
          </button>
        </div>
      </div>
    );
  };

  return (
    <div style={{ height: '600px' }} ref={calendarRef}>
    <Calendar
    localizer={localizer}
    events={calendarEvents}
    startAccessor="start"
    endAccessor="end"
    view={view}
    onView={setView}
    date={date}
    onNavigate={setDate}
    style={{ height: '100%' }}
    eventPropGetter={eventStyleGetter}
    onSelectEvent={(event) => {
        console.log('Calendar event clicked:', event); // 
        onSelectEvent(event.resource);
    }}
    onSelectSlot={onSelectSlot}
    selectable
    components={{
        toolbar: CustomToolbar,
    }}
    messages={{
        today: '',
        previous: '',
        next: '',
        month: '',
        week: '',
        day: '',
        agenda: '',
        date: '',
        time: '',
        event: '',
        noEventsInRange: '',
    }}
    />
    </div>
  );
};